import {Delimiter, EMPTY_STRING} from '../constant/Constant'

export const Base64 = {
    encode: (input: string) => {
        try {
            return btoa(input)
        } catch (e) {
            return EMPTY_STRING
        }
    },
    encodeUrlSafe: (input: string) => {
        let encoded = Base64.encode(input)
        return encoded.replace(/\+/g, '-') // + => -
            .replace(/\//g, '_') // / => _
            .replace(/=+$/, '') // remove padding
    },
    decode: (input: string) => {
        try {
            return atob(input)
        } catch (e) {
            return EMPTY_STRING
        }
    },
    decodeUrlSafe: (input: string) => {
        let replaced = input.replace(/-/g, '+') // - => +
            .replace(/_/g, '/') // _ => /
        // add padding
        while (replaced.length % 4 !== 0) {
            replaced += Delimiter.EQUALS
        }

        return Base64.decode(replaced)
    }
}

export const Url = {
    encode: (input: string) => {
        return encodeURIComponent(input)
    },
    decode: (input: string) => {
        return decodeURIComponent(input)
    },
}